import React from "react";
import RenderMenu from './render-menu'
import { Menu } from "antd";
import { withRouter } from "react-router";

 class ADLeftMenu extends React.Component {
  state = {
    current: "mail",
  };
  // 选择菜单
  handleClick = e => {
    console.log("click ", e);
    console.log(this.props)
    let { history } = this.props
    let targetProps = e.item.props   
    this.setState({
      current: e.key
    });
    if(targetProps.path){
      history.push(`/ant-design${targetProps.path}`)     
    } else {
      history.push(`/ant-design/no-page`)      
    }
  };
  render() {
    let { menu } = this.props
    return (
      /* 
         selectedKeys：当前选中的菜单 ['mail']
         mode：菜单类型,水平horizontal 垂直vertical 内联inline
      */
      menu.length>0&&<div className="left-menu">
      <Menu
        onClick={this.handleClick}
        selectedKeys={[this.state.current]}
        theme="dark"
        mode="inline"
        className="inline-menu"
      >
        { RenderMenu(menu) }
      </Menu>
    </div>
    );
  }
}
export default withRouter(ADLeftMenu)
