// 折叠面板
import React from "react";
import { Collapse, Icon,  Select } from "antd";
import CodeCollapse from './example/code-collapse'
const { Panel } = Collapse;
const { Option } = Select;

const genExtra = props => {
  // console.log("2222", props);
  return (
    <div>
      <Icon
        type="setting"
        onClick={event => {
          // If you don't want click extra trigger collapse, you can prevent this:
          event.stopPropagation();
        }}
      />
      <Select
        value={props.expandIconPosition}
        onChange={props.onPositionChange}
      >
        <Option value="left">left</Option>
        <Option value="right">right</Option>
      </Select>
      {/* <Dropdown overlay={menu}>
        <a className="ant-dropdown-link" href="javascript:;">
          Hover me <Icon type="down" />
        </a>
      </Dropdown> */}
    </div>
  );
};
const text = `
  A dog is a type of domesticated animal.
`;

export default class ADCollapse extends React.Component {
  state = {
    expandIconPosition: "left"
  };
  callback = key => {
    console.log(key);
  };
  onPositionChange = expandIconPosition => {
    this.setState({ expandIconPosition });
  };
  render() {
    const { expandIconPosition } = this.state;
    return (
      <div className="ad-collapse common-warp">
        <div className="example1 box">
          <Collapse
            defaultActiveKey={["1"]}
            onChange={this.callback}
            expandIconPosition={expandIconPosition}
          >
            <Panel
              header={<span>This is panel header 1</span>}
              key="1"
              extra={genExtra({
                expandIconPosition,
                onPositionChange: this.onPositionChange
              })}
            >
              <div>{text}</div>
            </Panel>
            <Panel
              header={<span>This is panel header 2</span>}
              key="2"
              extra={genExtra({
                expandIconPosition,
                onPositionChange: this.onPositionChange
              })}
            >
              <div>{text}</div>
            </Panel>
            <Panel
              header={<span>This is panel header 3</span>}
              key="3"
              extra={genExtra({
                expandIconPosition,
                onPositionChange: this.onPositionChange
              })}
            >
              <div>{text}</div>
            </Panel>
          </Collapse>
        </div>
        <CodeCollapse />
      </div>
    );
  }
}
